@media (min-width: 320px) {
  .logo {
    display: block;
    width: 230px;
    font-family: 'Proxima Nova Bold', 'Arial', Sans-serif;
    text-transform: uppercase;
    font-size: 36px;
    color: #000000;
    letter-spacing: 0.3em;
    position: relative;
  }
  .logo::after {
    content: "";
    width: 64px;
    height: 4px;
    position: absolute;
    background-color: #bea67c;
    z-index: 5;
    top: 19px;
    left: 45px;
  }
}
@media (min-width: 768px) {
  .logo {
    color: #ffffff;
  }
}
@media (min-width: 320px) {
  .main-nav {
    position: relative;
    width: 100%;
  }
  .main-nav__toggle {
    position: absolute;
    top: -50px;
    right: 0;
  }
  .main-nav__list {
    display: flex;
    width: 100%;
    flex-direction: column;
    text-decoration: none;
    list-style: none;
    background-color: transparent;
    padding: 0;
    margin: 0;
  }
  .main-nav__item {
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #ebebeb;
  }
  .main-nav__item:last-of-type {
    border-bottom: 1px solid #ebebeb;
  }
  .main-nav__link {
    display: block;
    font-family: 'Proxima Nova Bold', 'Arial', Sans-serif;
    font-size: 24px;
    text-transform: uppercase;
    text-decoration: none;
    padding-top: 20px;
    padding-bottom: 20px;
    color: #787878;
  }
  .main-nav__link:hover {
    color: #cdcdcd;
  }
}
@media (min-width: 768px) {
  .main-nav {
    width: 500px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    flex-direction: row;
    justify-content: flex-start;
  }
  .main-nav__item {
    border: none;
    padding-left: 4%;
  }
  .main-nav__item:last-of-type {
    border-bottom: none;
  }
  .main-nav__link {
    font-size: 15px;
    padding-top: 20px;
    padding-bottom: 20px;
    color: #787878;
  }
  .main-nav__link:hover {
    color: #cdcdcd;
  }
}
