.button-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 120px;
  height: 35px;
  font-family: 'Proxima Nova Bold', 'Arial', Sans-serif;
  font-size: 14px;
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}
.button-link::after {
  position: absolute;
  content: "";
  display: block;
  width: 50px;
  height: 2px;
  background-color: #bea67c;
  left: 0;
  bottom: 0;
}
.button-link:hover {
  cursor: pointer;
  opacity: 0.6;
}
.button-link:hover::after {
  width: 120px;
  transition: all 300ms ease;
}
